import { useQuery } from 'react-query';
import { useLocation } from 'react-router';
import { IGetMoviesResult, IGetTvsResult, searchMovie, searchTv } from '../api';
import Slider from '../Components/Slider';
import { Banner, Loader, Wrapper } from '../Components/styles';

function Search() {
  const location = useLocation();
  const keyword = new URLSearchParams(location.search).get('keyword');

  const { data: movies, isLoading: movieLoading } = useQuery<IGetMoviesResult>(
    ['movies', 'search', keyword],
    () => searchMovie(keyword || '')
  );

  const { data: tvs, isLoading: tvLoading } = useQuery<IGetTvsResult>(
    ['tvs', 'search', keyword],
    () => searchTv(keyword || '')
  );

  return (
    <Wrapper>
      {movies && tvLoading ? (
        <Loader>Loading...</Loader>
      ) : (
        <>
          <div style={{ height: '20vh' }}></div>

          {movies && <Slider category='Movie' contents={movies.results} />}
          {tvs && <Slider category='TV' contents={tvs.results} />}
        </>
      )}
    </Wrapper>
  );
}
export default Search;
