import { useViewportScroll } from 'framer-motion';
import { useHistory } from 'react-router-dom';
import {
  getMovieDetial,
  getTvDetial,
  IMovie,
  IMovieDetail,
  ITv,
  ITvDetail,
} from '../api';
import { makeImagePath } from '../utils';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import { useQuery } from 'react-query';

const Overlay = styled(motion.div)`
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
`;

const BigContentWrapper = styled(motion.div)`
  position: absolute;
  width: 40vw;
  height: 80vh;
  left: 0;
  right: 0;
  margin: 0 auto;
  border-radius: 15px;
  overflow: hidden;
  background-color: ${(props) => props.theme.black.veryDark};
`;

const BigCover = styled.div`
  width: 100%;
  background-size: cover;
  background-position: center center;
  height: 400px;
`;

const BigTitle = styled.div`
  color: ${(props) => props.theme.white.lighter};
  padding: 0 20px;
  padding-top: 20px;
  font-size: 40px;
  display: flex;
  align-items: center;
  position: relative;
`;

const Score = styled.div`
  color: ${(props) => props.theme.white.lighter};

  margin-bottom: 10px;
  margin-bottom: 20px;
  font-size: 20px;
  padding: 0 20px;
  font-weight: bold;
  color: #ff8af6;
  position: relative;
`;

const BigOverview = styled.p`
  padding: 0 20px;
  position: relative;
  color: ${(props) => props.theme.white.lighter};
`;

const BigTagline = styled.p`
  padding: 10px 20px;
  position: relative;
  font-size: 24px;
  color: ${(props) => props.theme.white.darker};
`;

interface IBigContentProps {
  id: string;
  category: string;
  type: 'movie' | 'tv';
}

function BigMovie({ id, category, type }: IBigContentProps) {
  const { data, isLoading } = useQuery<IMovieDetail>([type, 'detail', id], () =>
    type === 'movie' ? getMovieDetial(id) : getTvDetial(id)
  );
  const history = useHistory();
  const { scrollY } = useViewportScroll();
  const onOverlayClick = () => {
    history.goBack();
  };

  return (
    <>
      <Overlay
        onClick={onOverlayClick}
        exit={{ opacity: 0 }}
        animate={{ opacity: 1 }}
      />
      {!isLoading && data ? (
        <BigContentWrapper
          style={{ top: scrollY.get() + 100 }}
          layoutId={id + category}
        >
          <BigCover
            style={{
              backgroundImage: `linear-gradient(to top, black, transparent), url(${makeImagePath(
                data.backdrop_path || data.poster_path,
                'w500'
              )})`,
            }}
          />

          <BigTitle>
            {data.title} ({data.runtime} Hours)
          </BigTitle>
          <Score>
            Rating: {data.vote_average} ({data.vote_count} votes)
          </Score>
          <BigTagline>{data.tagline}</BigTagline>
          <BigOverview>{data.overview}</BigOverview>
        </BigContentWrapper>
      ) : (
        'Loading...'
      )}
    </>
  );
}

export default BigMovie;
