import { useQuery } from 'react-query';

import { AnimatePresence } from 'framer-motion';
import { getMovies, IGetMoviesResult } from '../api';
import { makeImagePath } from '../utils';
import { useRouteMatch } from 'react-router-dom';
import Slider from '../Components/Slider';
import { Banner, Loader, Overview, Title, Wrapper } from '../Components/styles';
import BigMovie from '../Components/BigMovie';

function Home() {
  const bigMovieMatch = useRouteMatch<{ movieId: string; category: string }>(
    '/movies/:movieId/:category'
  );

  const { data: nowPlayings, isLoading: nowPlayingsLoading } =
    useQuery<IGetMoviesResult>(['movies', 'nowPlaying'], () =>
      getMovies('now_playing')
    );

  const { data: populars, isLoading: popularLoading } =
    useQuery<IGetMoviesResult>(['movies', 'populars'], () =>
      getMovies('popular')
    );

  const { data: topRated, isLoading: topRatedLoading } =
    useQuery<IGetMoviesResult>(['movies', 'topRated'], () =>
      getMovies('top_rated')
    );

  const { data: upcomings, isLoading: upcomingLoading } =
    useQuery<IGetMoviesResult>(['movies', 'upcomings'], () =>
      getMovies('upcoming')
    );

  const findMovie = () => {
    if (!bigMovieMatch) return;

    if (bigMovieMatch.params.category === 'Now Playings')
      return nowPlayings?.results.find(
        (movie) => movie.id === +bigMovieMatch.params.movieId
      );
    else if (bigMovieMatch.params.category === 'Populars')
      return populars?.results.find(
        (movie) => movie.id === +bigMovieMatch.params.movieId
      );
    else if (bigMovieMatch.params.category === 'Top Rated')
      return topRated?.results.find(
        (movie) => movie.id === +bigMovieMatch.params.movieId
      );
    else if (bigMovieMatch.params.category === 'Upcomings')
      return upcomings?.results.find(
        (movie) => movie.id === +bigMovieMatch.params.movieId
      );
  };
  const clickedMovie = bigMovieMatch?.params.movieId && findMovie();

  return (
    <Wrapper>
      {nowPlayingsLoading ? (
        <Loader>Loading...</Loader>
      ) : (
        <>
          <Banner
            bgPhoto={makeImagePath(nowPlayings?.results[0].backdrop_path || '')}
          >
            <Title>{nowPlayings?.results[0].title}</Title>
            <Overview>{nowPlayings?.results[0].overview}</Overview>
          </Banner>

          {nowPlayings && (
            <Slider
              category='Now Playings'
              contents={nowPlayings.results.slice(1)}
            />
          )}

          {populars && (
            <Slider category='Populars' contents={populars.results} />
          )}

          {topRated && (
            <Slider category='Top Rated' contents={topRated.results} />
          )}

          {upcomings && (
            <Slider category='Upcomings' contents={upcomings.results} />
          )}

          <AnimatePresence>
            {clickedMovie && bigMovieMatch ? (
              <BigMovie
                id={bigMovieMatch?.params.movieId}
                category={bigMovieMatch?.params.category}
                type='movie'
              />
            ) : null}
          </AnimatePresence>
        </>
      )}
    </Wrapper>
  );
}
export default Home;
