import { useQuery } from 'react-query';

import { AnimatePresence, useViewportScroll } from 'framer-motion';
import { getMovies, getTv, IGetMoviesResult, IGetTvsResult, ITv } from '../api';
import { makeImagePath } from '../utils';
import { useHistory, useRouteMatch } from 'react-router-dom';
import Slider from '../Components/Slider';
import { Banner, Loader, Overview, Title, Wrapper } from '../Components/styles';
import BigTv from '../Components/BigTv';

function Tv() {
  const bigTvMatch = useRouteMatch<{ tvId: string; category: string }>(
    '/tv/:tvId/:category'
  );
  const { data: latest, isLoading: latestLoading } = useQuery<ITv>(
    ['tv', 'latest'],
    () => getTv('latest')
  );

  const { data: airing_today } = useQuery<IGetTvsResult>(
    ['tv', 'populars'],
    () => getTv('airing_today')
  );

  const { data: on_the_air } = useQuery<IGetTvsResult>(
    ['tv', 'on_the_air'],
    () => getTv('on_the_air')
  );

  const { data: populars } = useQuery<IGetTvsResult>(['tvs', 'populars'], () =>
    getTv('popular')
  );
  const { data: top_rated } = useQuery<IGetTvsResult>(['tv', 'upcomings'], () =>
    getTv('top_rated')
  );

  const findTv = () => {
    if (!bigTvMatch) return;

    if (bigTvMatch.params.category === 'Airing Today')
      return airing_today?.results.find(
        (tv) => tv.id === +bigTvMatch.params.tvId
      );
    else if (bigTvMatch.params.category === 'On The Air')
      return on_the_air?.results.find(
        (tv) => tv.id === +bigTvMatch.params.tvId
      );
    else if (bigTvMatch.params.category === 'Populars')
      return populars?.results.find((tv) => tv.id === +bigTvMatch.params.tvId);
    else if (bigTvMatch.params.category === 'Top Rated')
      return top_rated?.results.find((tv) => tv.id === +bigTvMatch.params.tvId);
  };
  const clickedTv = bigTvMatch?.params.tvId && findTv();

  return (
    <Wrapper>
      {latestLoading || !latest ? (
        <Loader>Loading...</Loader>
      ) : (
        <>
          <Banner
            bgPhoto={makeImagePath(latest.backdrop_path || latest.poster_path)}
          >
            <Title>{latest?.name}</Title>
            <Overview>{latest?.overview}</Overview>
          </Banner>

          {airing_today && (
            <Slider category='Airing Today' contents={airing_today.results} />
          )}

          {on_the_air && (
            <Slider category='On The Air' contents={on_the_air.results} />
          )}

          {populars && (
            <Slider category='Populars' contents={populars.results} />
          )}
          {top_rated && (
            <Slider category='Top Rated' contents={top_rated.results} />
          )}

          <AnimatePresence>
            {bigTvMatch && clickedTv ? (
              <BigTv
                id={bigTvMatch?.params.tvId}
                category={bigTvMatch?.params.category}
                type='tv'
              />
            ) : null}
          </AnimatePresence>
        </>
      )}
    </Wrapper>
  );
}
export default Tv;
